@import "utils/variables.scss";

.catalog {
  display: flex;
  flex-wrap: wrap;
  max-width: $catalog-max-width-value;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    transition-duration: $duration-time;

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }

  &__card:hover {
    color: $card-title-color;
    transform: scale(120%);
  }
}
